<template>
  <div id="container" style="height: 100vh;display: flex;align-items: center;justify-content: center ;">
    <div style="display:flex;width:25%;background:transparent">
      <!--      <div style="flex:1">
              <img src="@/assets/20201203214346381.jpg" style="width:100%"/>
            </div>-->
      <div style="flex:1;display: flex;align-items: center;justify-content: center;background:transparent">
        <el-form ref="loginRef" :model="user" :rules="rules" style="width: 70%">
          <div style="font-size: 20px;color: #fff3ea;text-align: center;font-weight: bold;margin-bottom: 20px">企业登录
          </div>
          <el-form-item prop="username">
            <el-input v-model="user.username" placeholder="请输入账号" prefix-icon="el-icon-s-custom"></el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input v-model="user.password" placeholder="请输入密码" prefix-icon="el-icon-lock" show-password></el-input>
          </el-form-item>

          <el-form-item>
            <el-button style="width: 100%;" type="primary" @click="login()">登录</el-button>
          </el-form-item>

          <div style="display: flex;height: 20px">
            <div style="flex: 1;text-align: left"><span style="cursor: pointer;color: #0f9876" @click="$router.push('/componentRegister')">企业注册</span></div>
            <div style="flex: 1;color: #0f9876;text-align: right"><span style="cursor: pointer;color: #0f9876" @click="$router.push('/login')">用户登录</span></div>
          </div>
        </el-form>

      </div>

    </div>
  </div>

</template>

<script>

import request from "@/utils/request";

export default {
  name: 'componentLogin',
  data: function () {
    return {
      user: {username: '', password: ''},
      rules: {
        username: [
          {required: true, message: '请输入账号', trigger: 'blur'},
          {min: 3, max: 10, message: '账号长度在 4 到 10 个字符之间', trigger: 'blur'}
        ], password: [
          {required: true, message: '请输入密码', trigger: 'blur'},
          {min: 6, max: 10, message: '密码长度在 6 到 10 个字符之间', trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    login() {
      this.$refs['loginRef'].validate(validate => {
        if (validate) {
          request.post('/component/login', this.user).then(res => {
            if (res.data.code === 1) {
              this.$router.push('/componentHomeView')
              this.$message.success("登录成功,欢迎你" + res.data.data.name)
              /*存储数据*/
              localStorage.setItem("kid-user", JSON.stringify(res.data))
            } else {
              this.$message.error(res.data.message)
            }
          })
        }
      })

    }
  },
}
</script>
<style scoped>
#container {

  background: url("../assets/R.jpg");
  background-size: 100vw;


}
</style>